* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root{
  --mainColor: crimson;
  --whiteColor: #fff;
  --blackColor: #000;
  --lightDark: #222;
  --bgColor: #fafafa;
}

body {
  font-family: sans-serif;
  background: var(--whiteColor);
  font-family: 'Lato', sans-serif;
}

nav {
  width: 100%;
  background:var(--whiteColor);
  height: 70px;
  line-height: 70px;
  position: fixed;
  top:0;
  box-shadow: 0 1px 3px rgba(0,0,0,.1);
  padding: 0 103px;
  display: flex;
  z-index: 9999;
}

nav ul {
  width: 50%;
}

nav .left {
  text-align: left;
}

nav .left li a {
  font-size: 20px;
  font-weight: bold;
  color: var(--mainColor);
}

nav .right {
  text-align: right;
}

nav ul li {
  display: inline-block;
  color: var(--lightDark);
}

nav ul li a {
  display: block;
  text-decoration: none;
  color: var(--lightDark);
}

nav ul li .shoppingCartTotal {
  display: inline-block;
  width: 35px;
  height: 35px;
  background: var(--mainColor);
  border-radius: 50%;
  color: var(--whiteColor);
  line-height: 35px;
  cursor: pointer;
  text-align: center;
  font-size: 13px;
}

.products {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding: 0 100px;
  margin: 30px 0;
}
.product {
  width: 25%;
  padding: 0 12px;
  margin: 20px 0;
  overflow: hidden;
}

.pro {
  position: relative;
  width: 100%;
  height: 100%;
}
.new {
  position: absolute;
  top: 0;
  left: 0;
  background:green;
  padding: 7px 15px;
  color: var(--whiteColor);
  font-size: 14px;
}

.hot {
  position: absolute;
  top: 0;
  left: 0;
  background:red;
  padding: 7px 15px;
  color: var(--whiteColor);
  font-size: 14px;
}

.pro {
  border-radius: 3px;
  overflow: hidden;
  background-color: var(--bgColor);
}

.productImage {
  background:var(--bgColor);
  width: 100%;
  min-height: 200px;
  text-align: center;
  overflow: hidden;
  /* padding: 10px; */
}
.productImage img {
  min-width: 100%;
  height: 200px;
}
.productDetails {
  padding: 15px 10px;
  text-align: center;
  background: var(--whiteColor)f;
  border-left: 1px solid var(--bgColor);
  border-right: 1px solid var(--bgColor);
  color: var(--whiteColor);
  color: var(--lightDark);
}

.proName {
  margin: 12px 0;
  color: var(--lightDark);
}

.proPrice {
  margin: 15px 0;
  color:var(--mainColor);
  font-size: 22px;
  font-weight: 30;
}

.proButton {
  width: 100%;
  background: var(--mainColor);
  margin: 0 auto;
  text-align: center;
  padding: 14px 0;
  cursor: pointer;
}

.proButton button {
    background-color: transparent;
    border: none;
    color: var(--whiteColor);
    text-transform: uppercase;
    outline: none;
    cursor: pointer;
}

/* Cart */

.cartContainer {
  width: 100%;
  margin-top: 100px;
  padding: 0 104px;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}

.cartDetails {
  width: 75%;
  border: 1px solid var(--bgColor);
  border-radius: 5px;
}

.cart {
  display: flex;
  align-items: center;
  background: var(--whiteColor);
  padding: 15px 20px;
}

.cartProImage {
  display: inline-block;
  width: 80px;
  height: 80px;
  position: relative;
}
.imageCount {
  position: absolute;
  display: block;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 35px;
  height: 35px;
  background:var(--mainColor);
  line-height: 35px;
  text-align: center;
  border-radius: 4px;
  color: var(--whiteColor);
  visibility: hidden;
  transition: all .5s;
}
.cartProImage:hover .imageCount {
  visibility: visible;
  top: 50%;
}
.cartProImage img {
  width: 100%;
  height: 100%;
}

.cartProductName {
  width: 250px;
  padding: 0 15px;
  font-size: 14px;
}
.cartProductPrice {
  width: 120px;
  padding: 0 15px;
  font-weight: bold;
}

.inc,
.dec {
  display: inline-block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  border: 1px solid var(--mainColor);
  color: var(--mainColor);
  border-radius: 50%;
  cursor: pointer;
  font-weight: normal;
}
.productQuantity {
  display: inline-block;
  padding: 0 15px;
  font-size: 16px;
  width: 70px;
  text-align: center;
}

.productTotalPrice {
  display: inline-block;
  width: 150px;
  padding: 0 15px;
  font-weight: bold;
  color: var(--mainColor);
  text-align: center;
}
.deleteCartPro {
  border:none;
  display: inline-block;
  padding: 0 15px;
  color: var(--mainColor);
  background: transparent;
  font-size: 20px;
  cursor: pointer;
}

.cartSummary {
  width: 25%;
  padding: 0 0 0 20px;
}

.summary {
  background: var(--whiteColor);
  padding: 13px;
}

.summary h3 {
  font-size: 18px;
  font-weight: normal;
  border-bottom: 1px solid #999;
  padding-bottom: 7px;
}
.totalItems {
  display: flex;
  margin: 15px 0;
}
.items {
  width: 50%;
  text-align: left;
  font-weight: normal;
}
.itemsCount {
  width: 50%;
  text-align: right;
  font-weight: bold;
  color: var(--mainColor);
}
.totalPriceSection {
  display: flex;
  margin: 15px 0;
}
.justTitle {
  width: 50%;
  text-align: left;
  font-weight: normal;
}
.itemsPrice {
  font-weight: bold;
  width: 50%;
  text-align: right;
  color: var(--mainColor);
}
.stripSection {
  margin: 15px 0;
  border-top: 1px solid #999;
  padding-top: 15px;
}

.container {
  width: 100%;
  height: auto;
}

header {
  width: 100%;
  height: 300px;
  background: url("https://cdn.pixabay.com/photo/2017/01/28/02/24/japan-2014616_960_720.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  color: var(--whiteColor);
  position: relative;
  color: var(--whiteColor);
}

header .headerText {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .5);
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--whiteColor);
  text-align: center;
}

header h1 {
  margin-bottom: 7px;
  font-size: 35px;
  text-transform: uppercase;
  color:var(--whiteColor)f;
}


/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #ccc; 
  border-radius: 4px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #ccc; 
}

@media only screen and (max-width:768px) {
  
  nav{
    padding: 0 10px;
  }
  .nav .left li a {
    font-size: 15px;
}
  .products {
    display: block;
    width: 100%;
    padding: 5px 2px;
    margin: 5px 2px;
  }
  .product {
    margin-right: 10px;
    width: 100%;
    padding: 5px 2px;
    margin: 5px 2px;
    overflow: hidden;
  }
  .cartContainer {
    display: block;
  }
  .cart {
    display: block;
  }
  .cartProImage {
    display: block;
    width: 150px;
    height: 150px;
  }
  .cartProductName {
    margin-top: 15px;
    display: block;
    margin-left: 15px;
  }
  .cartProductPrice {
    margin-top: 5px;
    display: block;
    margin-left: 30px;
  }
  
  .inc,
  .dec {
    display: block;
    margin-top: 15px;
    align-self: center;
    margin-left: 50px;
  }
  .productQuantity {
    display: block;
    margin-top: 15px;
    margin-left: 35px;
  }
  
  .productTotalPrice {
    display: block;
    margin-top: 15px;
  }
  .deleteCartPro {
    display: block;
    margin-top: 15px;
    margin-left: 45px;
  }
  .cartSummary {
    width: 100%;
  }
  .totalItems {
    display: flex;
    margin: 15px 0;
  }
  .itemsCount {
    margin-top: 10px;
  }
  .itemsPrice {
    margin-top: 10px;
  }

}